*{
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif
}
.container{
    width: 90%;
    margin: 0;
    padding: 0;
    height: auto;
    display: flex;
    box-shadow: 4px 4px 12px 0px;
    border-radius: 1.3rem;
}
.login-page{
    
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgb(252, 253, 253);
}
.login-right,.login-left{
    width: 50%;
    height:69vh;
    box-sizing: border-box;
    padding: 25px;

}
.login-right{
    background-image: url('https://i.postimg.cc/2S7wMzWf/Screenshot-2022-04-03-19-25-42-53-f2cb81fb7cf38af7978f186f2a61634a.jpg');
    box-sizing: border-box;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: rgba(35, 81, 234, 0.927);
    border-radius: 1.3rem 0rem 0rem 1.3rem;
    box-sizing: border-box;
    padding: 0;
    opacity: 0.8;
}
.right-contant{
    box-sizing: border-box;
    border-radius: 1.3rem 0rem 0rem 1.3rem;
    padding: 25px;
    background-color: rgba(10, 60, 225, 0.956);
    height: 100%;
}
.login-left{
    background-color: rgba(244, 245, 248, 0.836);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0rem 1.3rem 1.3rem 0rem;
}
.form{
    min-width: 100%;
    padding: 25px 5px 25px 5px;
    position: relative;
    margin-inline: 1.5rem;
    text-align: center;
    height:auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.contant{
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 25px;
}
.input{
    width: 55%;
    padding: 15px 7px 15px 11px;
    background-color: rgb(198, 204, 211);
    border-radius: 0.6rem;
    border: none;
}
.login-form{
    row-gap: 12px;
}
/*  */
.logo-box{
    height: 120px;
}
.logo{
    width: 7.1rem;
}
.content-box{
    height: 100%;
    color: #fff;
    font-size: 0.9rem;
    height: auto;
}